body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-color: #FEFAE0;
}

.catch-phrase{
  font-size: 180px;
  text-align: left;
  font-family: 'Berkshire Swash';
  margin:0;
  padding-left: 120px;
  padding-top: 50px;
  color: rgb(255, 255, 255);
}

.catch-phrase-2{
  font-size: 100px;
  margin:0;
  padding-left: 170px;
  margin-bottom: 40px;
}

.landing-page {
  background-color: #FEFAE0;
  background-image: url(/static/media/landing-background.59e261f6.jpg);
  min-height: 100vh;
  align-items: center;
  justify-content: center;
}



.recipe{
    display:block;
    font-size: 20px;
    background-color: rgba(255, 255, 255, 0.733);
}
#ingredients {
    margin-top: 50px;
    margin-left: auto;
    margin-right: auto;
    border-collapse: collapse;
}

.ingredient-data{
    margin-top: 100px;
}


.recipe-title{
    font-family: 'Berkshire Swash';
    font-size: 50px;
    margin-top: 10px;
}

.header-words{
    font-family: "Walter Turncoat", bold;
    font-size: s28px;
}

.table-entries{
    font-family: "Coming Soon";
    font-size: 22px;
    border: 2px solid rgba(126, 127, 134, 0.432);
}

.checkbox input[type=checkbox] {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    /* create custom checkbox appearance */
    width: 25px;
    height: 25px;
    /* background-color only for content */
    border-radius: 6px;
    background-color: #ffffff;
}

.checkbox input[type=checkbox]:checked{
    background-color: rgb(100, 24, 24);
}

.checkbox:focus {
    outline: 0 !important;
}

.finalize-button{
    width:200px;
    height:60px;
    margin-bottom: 100px;
    border-radius: 6px;
    border:none;
    margin-top: 30px;
    background-color: rgb(95, 158, 160);
    color:white;
    font-size: 60px;
    font-family: "Walter Turncoat";
}

.finalize-button:focus {
    outline: 0 !important;
}

.finalize-button:hover{
    background-color: rgb(57, 114, 116);
}

.sub{
    padding-left: 0;
}
.popup{
    position: fixed;
    top:50%;
    left:50%;
    width: 400px;

    border: none;
    border-radius: 4px;
    box-shadow: -2px 5px 5px #a1a1a1;

    background-color: #FDFFFC;

    align-items: left;

    color: #011627;
    font-size: 20px;
    font-family: 'Coming Soon';
    text-align: left;
}


.recipe-steps{
    background-color: rgb(255, 255, 255);
    padding-bottom: 90px;
    padding-left: 60px;
    padding-right:40px;
    text-align: left;
    font-family: 'Coming Soon';
    font-size: 23px;
}


.ingredients-with-measure{
    text-align: left;
    display: block;
    padding-left: 100px;
    padding-top:50px;
    padding-bottom:40px;
    background-color: white;
    font-family: 'Coming Soon';
    font-size: 18px;
}
.instruction{
    font-size: 40px;
    font-family: 'Walter Turncoat';
    color: white;
    background-color: rgba(95, 158, 160, 0.459);
    margin-bottom: 40px;
}

.field{
    display: inline-block;
    width:60%;
    height: 35px;
    position: relative;
    border-radius: 4px;
    background-color: rgba(255, 255, 255, 0.726);
    border:none;
    font-size: 25px;
    color:rgba(0, 0, 0, 0.616);
    font-family:"Coming Soon"
}

.field:hover{
    background-color: rgb(255, 255, 255);
}

.field:active{
    background-color: rgb(255, 255, 255);
}

.field:focus {
    outline: 0 !important;
}

.form-input{
    text-align: center;
}
